<template>
    <div>
        <table class="table border">
            <tr>
                <th class="w-25 text-center align-middle" style="background: rgba(241,241,241);">
                    
                </th>
                <td>
                    <b-input-group>
                        <b-form-input type="text" v-model="search" placeholder=" ." @keydown.enter="searchPurchase"></b-form-input>
                        <b-input-group-append>
                            <b-button variant="outline-secondary" @click="searchPurchase"></b-button>
                        </b-input-group-append>
                    </b-input-group>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    export default {
        name: "PurchaseSearchTask",
        data() {
            return {
                search:""
            }
        },
        methods: {
            searchPurchase(){
                this.$emit("searchPurchase", this.search);
            }
        },
    }
</script>

<style scoped>

</style>


