<template>
    <div class="d-inline float-right">
        <b-button v-b-modal.modal-1 variant="transparent" class="btn-outline-secondary"> </b-button>

        <b-modal id="modal-1" size="xl" :title="info.title" hide-footer="" ref="modal-1"
        centered footer-class="m-auto" >
            <div style="width:90%;margin:1% auto;">
                <h2> </h2>
                <hr />
                <table class="table">
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <!-- <b-form-select v-model="item.tbCustomer_ID" :options="$store.state.clientName"></b-form-select> -->
                            <b-form-input v-model="item.bname" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <!-- <b-form-select v-model="item.bName" :options="purchase"></b-form-select> -->
                            <b-form-input v-model="item.bnumber" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.chief" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             ()
                        </th>
                        <td>
                            <b-form-input v-model="item.userID" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.manager" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.mobile" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            /
                        </th>
                        <td>
                            <b-form-input v-model="item.priceRate" type="number" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.bSector" placeholder=""></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <!-- <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            ( )
                        </th>
                        <td>
                            <b-form-input v-model="item.userID"  placeholder="()"></b-form-input>
                        </td> -->
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td colspan="3">
                            <b-form-input type="password" v-model="item.userPW" placeholder=""></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td colspan="3">
                            <b-form-input v-model="item.postNo" placeholder=""></b-form-input>
                            <br>
                            <b-form-input v-model="item.address1" placeholder="/"></b-form-input>
                            <br>
                            <b-form-input v-model="item.address2" placeholder=" "></b-form-input>
                            <br>
                            <b-form-input v-model="item.address3" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                </table>
                <h2>/  </h2>
                <hr />
                <table class="table">
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-select v-model="item.tbDeliverer_ID" :options="delivererList"></b-form-select>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-select v-model="item.salesman_ID" :options="salesmanList"></b-form-select>
                        </td>
                    </tr>
                </table>
                <div class="text-center">
                    <b-button class="btn btn-success w-25" @click="reqOrder"></b-button>
                    <b-button class="btn btn-warning w-25" @click="cancel"></b-button>
                </div>
            </div>
            
        </b-modal>
    </div>
</template>

<script>
    import Constant from '../../../Constant';
    export default {
        name: "Client",
        props: ["info"],
        computed: {
            item(){
                var data = Object.assign({}, this.$store.state.clientItem);
                console.log(data);
                return data;
            },
            delivererList(){
                var data = [];
                this.$store.state.delivererList.forEach(element => {
                    data.push({text: element.bname, value: element.id})
                });
                return data;
            },
            salesmanList() {
                var data = [];
                this.$store.state.employeeList.forEach(element => {
                    data.push({text: element.name, value: element.id})
                });
                return data;
            }
        },
        data() {
            return {
            
            }
        },
        methods: {
            reqOrder(){
                this.item.tel = this.item.userID;
                console.log(this.item);
                this.$store.dispatch(Constant.INSERT_CLIENT, this.item);
                
                this.$refs['modal-1'].hide();
            },
            cancel() {
                this.$refs['modal-1'].hide()
                Object.assign(this.item, this.$store.state.clientItem);
            }
        },
    }
</script>



