<template>
    <div style="width:90%;margin:1% auto;">
        <div><span class="h2"> </span> <button class="d-inline btn btn-outline-danger float-right" @click="deleteProvider"></button></div>
        <hr />
                <table class="table">
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-select v-model="item.tbCustomer_ID" :options="clientList"></b-form-select>
                            <!-- <b-form-input v-model="item.bname" placeholder=" "></b-form-input> -->
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-select v-model="item.orderState" :options="orderState"></b-form-select>
                            <!-- <b-form-input v-model="item.bname" placeholder=" "></b-form-input> -->
                        </td>
                        
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-select v-model="item.payment" :options="payment"></b-form-select>
                            <!-- <b-form-input v-model="item.manager" placeholder=" "></b-form-input> -->
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.orderDate" type="date" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td colspan="3">
                            <b-form-textarea
                                v-model="item.requests"
                                placeholder=""
                                rows="2" />
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td colspan="3">
                            <b-form-textarea
                                v-model="item.memo"
                                placeholder=""
                                rows="2" />
                        </td>
                    </tr>
                </table>
                <!-- <OrderGoodsTable 
                    :list="goodsList"
                    :fields="fields"
                    :rows="rows"
                    :selcet="'multi'"
                    @rowSelected="rowSelected"
                /> -->
                <b-list-group>
                    <b-list-group-item>  <order-modal @selectDone="selectDone" @rowSelected="rowSelected"/>
                    </b-list-group-item>
                    <template v-for="(item, index) in orderList"> 
                        <b-list-group-item>
                            <b-media tag="li">
                                <table class="table w-100 text-center">
                                    <!-- {{item}} -->
                                    <tr class="w-100 text-center">
                                        <td class="w25 align-middle">
                                            {{index+1}}
                                        </td>
                                        <td class="w-25 align-middle">
                                            <b-img slot="aside" blank blank-color="#abc" width="64" alt="placeholder"></b-img>
                                        </td>
                                        <td class="w-25 align-middle">
                                            {{item.itemName+"("+item.tbitem_ID+")"}}
                                        </td>
                                        <td class="w-25 align-middle">
                                            {{nwc(item.price)}}
                                        </td>
                                        <td class="w-25 align-middle">
                                                <b-button-group>
                                                <b-button @click="decrement(item.tbitem_ID)">-</b-button>
                                                <b-button>{{item.qty}}</b-button>
                                                <b-button @click="increment(item.tbitem_ID)">+</b-button>
                                                </b-button-group>
                                        </td>
                                        <td class="w-25 align-middle">
                                            {{nwc(item.amount)}}
                                        </td>
                                    </tr>
                                </table>
                            </b-media>
                        </b-list-group-item>      
                    </template>
                </b-list-group>
                <table class="table">
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td colspan="3">
                            <!-- <b-form-select v-model="item.tbCustomer_ID" :options="clientList"></b-form-select> -->
                            <b-form-input v-model="qty"></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td colspan="3">
                            <!-- <b-form-select v-model="item.tbCustomer_ID" :options="clientList"></b-form-select> -->
                            <b-form-input v-model="total"></b-form-input>
                        </td>
                    </tr>
                </table>
                <div class="text-center">
                    <b-button class="btn btn-success w-25" @click="reqRegister"></b-button>
                    <b-button class="btn btn-warning w-25" @click="cancel"></b-button>
                </div>
    </div>
</template>

<script>
    import Constant from '../../../Constant.js';
    import OrderGoodsTable from './OrderGoodsTable.vue';
    import OrderModal from './OrderModal';
    import {pmOrderItamVO} from '../../../vuex/pmOrderItamVO';
    import pmOrder from '../../../vuex/pmOderVO';
    export default {
        name: "OrderDetail",
        components: {
            OrderGoodsTable,
            OrderModal
        },
        computed: {
            item() {
                console.log("id"+this.$store.state.order);
                return this.$store.state.order;
            },
            list() {
                var data = [];
                this.$store.state.order
            },
            clientList() {
                var data = [];
                this.$store.state.clientList.forEach(element => {
                    data.push({text: element.bname, value:element.id});
                });
                return data;
            },
            orderList() {
                console.log(this.$store.state.order.pmOrderItamVOs);
                return this.$store.state.order.pmOrderItamVOs;
            },
            rows(){
                return this.$store.state.order.pmOrderItamVOs.length;
            },
            fields() {
                return this.$store.state.orderItemFiled;
            },
            total() {
                var total =0
                if(this.$store.state.order.pmOrderItamVOs.length === 0){
                    return 0;
                }
                this.$store.state.order.pmOrderItamVOs.forEach(item => {
                    total += parseInt(item.amount);
                })
                return total;
            },
            qty() {
                var total =0
                if(this.$store.state.order.pmOrderItamVOs.length === 0){
                    return 0;
                }
                this.$store.state.order.pmOrderItamVOs.forEach(item => {
                    total += parseInt(item.qty);
                })
                return total;
            }
        },
        data() {
            return {
                payment: [
                    {
                        text: "",
                        value: "",
                    },
                    {
                        text: "",
                        value: "",
                    },
                ],
                cnt: 0,
                orderState: [
                    {
                        text: "",
                        value: ""
                    },
                    {
                        text: "",
                        value: ""
                    },
                    {
                        text: "",
                        value: ""
                    },
                    {
                        text: "",
                        value: ""
                    },
                    {
                        text: "",
                        value: ""
                    },
                ],
                orderItem: []
            }
        },
        methods: {
            reqRegister() {
                var postData = this.item;
                postData.pmOrderItamVOs = this.orderList;
                console.log(this.orderList);

                console.log(this.total);
                postData.amount = Number(this.total);
                postData.payment = Number(this.total);
                postData.itemCount = this.qty;
                postData.pmOrderItemJoinItemVOs = null;
                console.log(postData);
                this.$store.dispatch(Constant.UPDATE_ORDER, postData);
            },
            cancel() {
                this.$store.commit(Constant.CHANGE_PAGE, {component:"Order"});
            },
            nwc(x) {
                return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            },
            deleteProvider() {
                console.log(this.item.id);
                this.$store.dispatch(Constant.DELETE_ORDER, this.item.id);
            },
            rowSelected(item) {
                this.orderItem = item;
            },
            selectDone(){
                console.log(this.orderItem);
                for(var i=0; i < this.orderItem.length; i++){
                    let test = this.orderList.findIndex((element) => {
                        return this.orderItem[i].id === element.tbitem_ID;
                    });
                    // console.log(test);
                    // console.log(test);
                    if(test === -1){
                        console.log(this.orderItem[i]);
                        this.$store.commit(Constant.ORDER_ITEM_ADD, new pmOrderItamVO(this.orderItem[i].id, this.orderItem[i].itemName, this.orderItem[i].price2, 1, (this.orderItem[i].price2*1), this.orderItem[i].state, this.orderItem[i].memo, this.orderItem[i].remark1, this.orderItem[i].remark2))
                        // this.orderList.push(new pmOrderItamVO(this.orderItem[i].id, this.orderItem[i].itemName, this.orderItem[i].price2, 1, (this.orderItem[i].price2*1), this.orderItem[i].state, this.orderItem[i].memo, this.orderItem[i].remark1, this.orderItem[i].remark2));
                    }
                }
                console.log(this.orderList);
                console.log("selectDone");
            },
            increment(id) {
                console.log("increment active");
                console.log(id);
                this.$store.commit(Constant.UPDATE_ITEM_QTY, {id: id, val: "up"});
            },
            decrement(id){
                console.log("decrement active");
                console.log(id);
                this.$store.commit(Constant.UPDATE_ITEM_QTY, {id: id, val: "down"});
            }
        },
    }
</script>

<style scoped>

</style>



