<template>
    <div class="d-inline float-right">
        <b-button v-b-modal.modal-1 variant="transparent" class="btn-outline-secondary"> </b-button>
        <b-modal id="modal-1" size="xl" title=" " hide-footer="" ref="modal-1"
        centered footer-class="m-auto" >
            <div style="width:90%;margin:1% auto;">
                <h2> </h2>
                <hr />
                <GoodsTable 
                    :list="goodsList"
                    :fields="fields"
                    :rows="rows"
                    :selcet="'multi'"
                    @rowSelected="rowSelected"
                />
                
                <div class="text-center">
                    <b-button class="btn btn-success w-25" @click="reqRegister"></b-button>
                    <b-button class="btn btn-warning w-25" @click="cancel"></b-button>
                </div>
            </div>
            
        </b-modal>
    </div>
</template>

<script>
    import Constant from '../../../Constant';
    import GoodsTable from './OrderGoodsTable.vue';
    export default {
        name: "OrderModal",
        props: ["info"],
        components: {
            GoodsTable
        },
        computed: {
            item(){
                var data = Object.assign({}, this.$store.state.orderItem);
                return data;
            },
            clientList() {
                var data = [];
                this.$store.state.clientList.forEach(element => {
                    data.push({text: element.bname, value:element.id});
                });
                return data;
            },
            goodsList() {
                return this.$store.state.goodsList;
            },
            rows(){
                return this.$store.state.goodsList.length;
            },
            fields() {
                return this.$store.state.goodsFields;
            },
            total() {
                if(this.orderList.length === 0){
                    return 0;
                }
                var total =0
                this.orderList.forEach(item => {
                    total += parseInt(item.price1);
                })
                return total;
            },
            qty() {
                return this.orderList.length;
            }
        },
        data() {
            return {
                payment: [
                    {
                        text: "",
                        value: "",
                    },
                    {
                        text: "",
                        value: "",
                    },
                ],
                orderList: [],
                cnt:0
            }
        },
        methods: {
            reqRegister(){
                this.$emit("selectDone");
                // this.store.dispatch(Constant.INSERT_ORDER, this.orderList);
                this.$refs['modal-1'].hide();
            },
            cancel() {
                this.$refs['modal-1'].hide()
            },
            rowSelected(item) {
                this.$emit('rowSelected', item);
            }
        },
    }
</script>



