<template>
    <div style="width:90%;margin:1% auto;">
        <div><span class="h2"> </span> <button class="d-inline btn btn-outline-danger float-right" @click="deleteProvider"></button></div>
        <hr />
        <table class="table">
            <tr>
                <th class="text-center align-middle" style="background: rgba(241,241,241);">
                     
                </th>
                <td>
                    <!-- <b-form-select v-model="item.tbCustomer_ID" :options="$store.state.clientName"></b-form-select> -->
                    <b-form-input v-model="item.bname" placeholder=" "></b-form-input>
                </td>
                <th class="text-center align-middle" style="background: rgba(241,241,241);">
                     
                </th>
                <td>
                    <!-- <b-form-select v-model="item.role" :options="roleList"></b-form-select> -->
                    <!-- <b-form-select v-model="item.bName" :options="purchase"></b-form-select> -->
                    <b-form-input v-model="item.bnumber" placeholder=" "></b-form-input>
                </td>
            </tr>
            <tr>
                <th class="text-center align-middle" style="background: rgba(241,241,241);">
                     
                </th>
                <td>
                    <b-form-input v-model="item.manager" placeholder=" "></b-form-input>
                </td>
                <th class="text-center align-middle" style="background: rgba(241,241,241);">
                     
                </th>
                <td>
                    <b-form-input v-model="item.tel" placeholder=" "></b-form-input>
                </td>
            </tr>
            <tr>
                <th class="text-center align-middle" style="background: rgba(241,241,241);">
                    /
                </th>
                <td>
                    <b-form-input v-model="item.bsector" placeholder="/"></b-form-input>
                </td>
                <th class="text-center align-middle" style="background: rgba(241,241,241);">
                    FAX 
                </th>
                <td>
                    <b-form-input v-model="item.fax" placeholder="FAX "></b-form-input>
                </td>
            </tr>
            <tr>
                <th class="text-center align-middle" style="background: rgba(241,241,241);">
                    
                </th>
                <td colspan="3">
                    <b-form-input v-model="item.postno" placeholder=""></b-form-input>
                    <b-form-input v-model="item.address1" placeholder="/"></b-form-input>
                    <b-form-input v-model="item.address2" placeholder=""></b-form-input>
                    <b-form-input v-model="item.address3" placeholder=""></b-form-input>
                </td>
            </tr>
        </table>
        <hr>
        
        <div class="text-center">
            <b-button class="btn btn-success w-25" @click="reqRegister"></b-button>
            <b-button class="btn btn-warning w-25" @click="cancel"></b-button>
        </div>
    </div>
</template>

<script>
    import Constant from '../../../Constant.js';
    export default {
        name: "ProviderDetail",
        computed: {
            item() {
                return this.$store.state.provider;
            }
        },
        methods: {
            reqRegister() {
                this.$store.dispatch(Constant.UPDATE_PROVIDER, this.item);
            },
            cancel() {
                this.$store.commit(Constant.CHANGE_PAGE, {component:"Provider"});
            },
            deleteProvider() {
                this.$store.dispatch(Constant.DELETE_PROVIDER, this.item.id);
            }
        },
    }
</script>

<style scoped>

</style>


