export class pmOrderItamVO{
    constructor(tbitem_ID, itemName, price, qty, amount, state, memo, remark1, remark2) {
        this.tbitem_ID= tbitem_ID;
        this.itemName = itemName;
        this.price= price;
        this.qty= qty;
        this.amount= amount;
        this.state= state;
        this.memo= memo;
        this.remark1= remark1;
        this.remark2= remark2;
    }
}


