<template>
    <div style="overflow-y:scroll">
        <div style="width:90%;margin:1% auto;">
            <div><span class="h2"> </span> <button class="d-inline btn btn-outline-danger float-right" @click="deleteGoods"></button></div>
                <hr />
                <table class="table">
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <!-- <b-form-select v-model="item.tbCustomer_ID" :options="$store.state.clientName"></b-form-select> -->
                            <b-form-input v-model="item.bname" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <!-- <b-form-select v-model="item.bName" :options="purchase"></b-form-select> -->
                            <b-form-input v-model="item.bnumber" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.chief" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.tel" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.manager" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.mobile" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            /
                        </th>
                        <td>
                            <b-form-input v-model="item.priceRate" type="number" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.bsector" placeholder=""></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            ( )
                        </th>
                        <td>
                            <b-form-input v-model="item.userID"  placeholder="()"></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.userPW" placeholder=""></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td colspan="3">
                            <b-form-input v-model="item.postNo" placeholder=""></b-form-input>
                            <br>
                            <b-form-input v-model="item.address1" placeholder="/"></b-form-input>
                            <br>
                            <b-form-input v-model="item.address2" placeholder=" "></b-form-input>
                            <br>
                            <b-form-input v-model="item.address3" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                </table>
                <h2>/  </h2>
                <hr />
                <table class="table">
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-select v-model="item.tbDeliverer_ID" :options="delivererList"></b-form-select>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-select v-model="item.salesman_ID" :options="salesmanList"></b-form-select>
                        </td>
                    </tr>
                </table>
                <div><span class="h2">  </span> <ClientItemModal /> </div>
                <hr/>
                <ClientItemTable
                    :list="clientItemList"
                    :row="clientItemList.length"
                    :fields="clientItemFiled"
                    @rowSelected="rowSelected"
                />
                <div class="text-center">
                    <b-button class="btn btn-success w-25" @click="reqUpdate"></b-button>
                    <b-button class="btn btn-warning w-25" @click="cancel"></b-button>
                </div>
            </div>
    </div>
</template>

<script>
    import Constant from '../../../Constant';
    import ClientItemTable from './ClientItemTable.vue';
    import ClientItemModal from './ClientItemModal.vue';
    export default {
        name: "ClientDetail",
        components: {
            ClientItemTable,
            ClientItemModal
        },
        computed: {
            item(){
                return this.$store.state.client;
            },
            delivererList(){
                var data = [];
                this.$store.state.delivererList.forEach(element => {
                    data.push({text: element.bname, value: element.id})
                });
                return data;
            },
            salesmanList() {
                var data = [];
                this.$store.state.employeeList.forEach(element => {
                    data.push({text: element.name, value: element.id})
                });
                return data;
            },
            clientItemList() {
                return this.$store.state.clientItemList;
            },
            clientItemFiled() {
                return this.$store.state.clientItemFiled;
            }
        },
        methods: {
            reqUpdate(){
                this.$store.dispatch(Constant.UPDATE_CLIENT, this.item);
            },
            cancel() {
                this.$store.commit(Constant.CHANGE_PAGE, {component: "client"})
            },
            deleteGoods() {
                console.log(this.client);
                this.$store.dispatch(Constant.DELETE_CLIENT, this.item.id);
            },
            rowSelected(item) {
                console.log(item);
                var a = confirm(" ?");
                if(a){
                    this.$store.dispatch(Constant.DELETE_CLIENT_ITEM, item);   
                }
            }
        },
        mounted() {
            console.log(this.item);
        },
    }
</script>



