<template>
    <div class="d-inline float-right">
        <b-button v-b-modal.modal-1 variant="transparent" class="btn-outline-secondary"> </b-button>

        <b-modal id="modal-1" size="xl" title=" " hide-footer="" ref="modal-1"
        centered footer-class="m-auto" >
            <div style="width:90%;margin:1% auto;">
                <h2></h2>
                <hr />            
                <table class="table">
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-select v-model="item.tbCustomer_ID" :options="clientName"></b-form-select>
                            <!-- <b-form-input v-model="item.itemName" placeholder=""></b-form-input> -->
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-select v-model="item.bName" :options="purchase"></b-form-select>
                            <!-- <b-form-input v-model="item.itemName" placeholder=""></b-form-input> -->
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.itemName" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.itemQTY" placeholder=""></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.manufacturer" placeholder=""></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td>
                            <b-form-input v-model="item.origin" placeholder=""></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.spec" placeholder=" "></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.unit" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.price1" placeholder=" "></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.price2" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.price3" placeholder=" "></b-form-input>
                        </td>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                             
                        </th>
                        <td>
                            <b-form-input v-model="item.purchasePrice" placeholder=" "></b-form-input>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);">
                            
                        </th>
                        <td colspan="3">
                            <b-form-textarea
                                v-model="item.memo"
                                placeholder="  "
                                rows="3" />
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);"></th>
                        <td colspan="3">
                            <b-row>
                                <b-col lg="3">
                                    <b-form-select v-model="item.first" :options="select.fCategory.options"></b-form-select>
                                </b-col>
                                <b-col lg="3">
                                    <b-form-select v-model="item.second" :options="select.sCategory.options"></b-form-select>
                                </b-col>
                            </b-row>
                        </td>
                    </tr>
                    <tr>
                        <th class="text-center align-middle" style="background: rgba(241,241,241);"></th>
                        <td colspan="3">
                            <input type="file" ref="photofile" name="photo">
                        </td>
                    </tr>
                </table>
                <div class="text-center">
                    <b-button class="btn btn-success w-25" @click="reqOrder"></b-button>
                    <b-button class="btn btn-warning w-25" @click="$refs['modal-1'].hide()"></b-button>
                </div>
            </div>
            
        </b-modal>
    </div>
</template>

<script>
    import Constant from '../../../Constant';
    export default {
        name: "GoodsModal",
        props: ["info"],
        computed: {
            item(){
                var data = Object.assign({}, this.$store.state.item);
                return data;
            },
            purchase() {
                var data = [];
                this.$store.state.providerList.forEach(element => {
                    data.push({text: element.bname, value: element.bname});
                });
                console.log(data);
                return data;
            },
            clientName() {
                var data = [];
                this.$store.state.clientList.forEach(element => {
                    data.push({text: element.bname, value: element.id});
                });
                console.log(data);
                return data;
            }
        },
        data() {
            return {
                file: "",
                select: {
                    fCategory: {
                        selected: 'all',
                        options: [
                            { text: '', value: 'all' },
                            { text: '1', value: '1' },
                            { text: '2', value: '2' },
                            { text: '3', value: '3' }
                        ],
                    },
                    sCategory: {
                        selected: 'all',
                        options: [
                            { text: '', value: 'all' },
                            { text: '1', value: '1' },
                            { text: '2', value: '2' },
                            { text: '3', value: '3' }
                        ],
                    }
                }
            }
        },
        methods: {
            reqOrder(){
                var formData = new FormData();
                for (const key in this.item) {
                    const element = this.item[key];
                    console.log(key+", "+element);
                    formData.append(key.toString(), element);
                }
                if(this.item !== "undefined"){
                    var file = this.$refs.photofile.files[0];
                    this.item.itemImage = file;
                    formData.append("itemImage", file);
                }
                // console.log(this.item);
                // console.log(this.$store.state.item);
                this.$store.dispatch(Constant.INSERT_GOODS, {formData: formData, item: this.item});
                this.$refs['modal-1'].hide();
            }
        },
    }
</script>



